import './videos.css';
import album_1 from './../../img/album_1.jpg'
import album_2 from './../../img/album_2.jpg'
import album_3 from './../../img/album_3.jpg'
import album_4 from './../../img/album_4.jpg'
import album_5 from './../../img/album_5.jpg'
import album_6 from './../../img/album_6.jpg'
import album_7 from './../../img/album_7.jpg'
import album_8 from './../../img/album_8.jpg'
import album_9 from './../../img/album_9.jpg'
import { Swiper, SwiperSlide } from 'swiper/react';
import React, { useState } from 'react';
import { motion } from 'framer-motion';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/grid';
import 'swiper/css/pagination';

import { Grid, Pagination } from 'swiper/modules';

const textAnimarion = {
    hidden: {
        x: -900,
        opacity: 0,
    },
    visible: custom => ({
        x: 0,
        opacity: 1,
        transition: { delay: custom*0.5 },
    }),
  }

const Videos = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);
  const [currentImage, setCurrentImage] = useState(null);

  const handleImageClick = (imageSrc) => {
    setCurrentImage(imageSrc);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setCurrentImage(null);
    setIsModalOpen(false);
  };
    return (
        <motion.div 
        initial="hidden"
        whileInView="visible"
        viewport={{ amount: 0.3, once: true }}
        className="videos"> 
            <div id='videos' className="parallax__seven">
                <div className="videos__main">
                        <div className="container">
                            <div className="headline_seconds">
                                <motion.h1 custom={1} variants={textAnimarion}>НАШИ ВИДЕО И АЛЬБОМЫ ↓</motion.h1>
                            </div>
                        </div>
                </div>
            </div>
            <div className="container">
                <div className="videos-content">
                <motion.iframe custom={2} variants={textAnimarion} src="https://www.youtube.com/embed/gxOO7waYYtg?si=Ec6dPEcRwiOlOL12" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></motion.iframe>
                <motion.iframe custom={2} variants={textAnimarion} src="https://www.youtube.com/embed/VcS07o95JzQ?si=TCzIY_ohfaOUIqOv" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></motion.iframe>
                <motion.iframe custom={2} variants={textAnimarion} src="https://www.youtube.com/embed/YrkpciMPLeg?si=epmcYIafniV43Is1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></motion.iframe>
                </div>
                <motion.h2 custom={3} variants={textAnimarion} className='headline_album'>АЛЬБОМЫ</motion.h2>
                <div className="albums">
                <Swiper
                    slidesPerView={1}
                    grid={{
                    rows: 2,
                    }}
                    spaceBetween={30}
                    pagination={{
                    clickable: true,
                    }}
                    modules={[Grid, Pagination]}
                    className="mySwiper"
                >
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_1)}><img src={album_1} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_2)}><img src={album_2} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_3)}><img src={album_3} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_4)}><img src={album_4} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_5)}><img src={album_5} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_6)}><img src={album_6} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_7)}><img src={album_7} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_8)}><img src={album_8} alt="" /></SwiperSlide>
                    <SwiperSlide className='slide' onClick={() => handleImageClick(album_9)}><img src={album_9} alt="" /></SwiperSlide>
                </Swiper>
                {isModalOpen && (
                    <div className="modal-overlay" onClick={handleCloseModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        <img src={currentImage} alt="" className="modal-image" />
                        <button className="close-button" onClick={handleCloseModal}>
                        X
                        </button>
                    </div>
                    </div>
                )}
                </div>
            </div>
        </motion.div>
    )
}

export default Videos;